.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
  }
  
  .spinner {
    width: 40px;
    height: 40px;
    border: 4px solid white;
    border-top: 4px solid #5d62bc;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }